@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;800&display=swap);
h1, h2, h3 {
    font-family: 'Nunito', sans-serif;
}

.site {
    min-height: 100vh;
    margin-bottom: -40px;
}

footer, .monSite {
    height: 40px;
}

footer {
    display: flex;
    justify-content: space-between;
}

footer div {
    padding: 2px;
}

.flex {
    display: flex;
}

.bouton {
    width: 100%;
    text-align: center;
}

.select {
    display: flex;
    justify-content: center;
}

.formControl {
    width: 78%;
}

.formLabel {
    text-align: center;
    margin-right: 10px;
    padding-top: 8px;
    padding-bottom: 12px;
}

.boutonLocalisation {
    text-align: center;
}

.row {
    margin-top: 12px;
    margin-bottom: 12px;
}

.col {
    margin-bottom: 12px;
    min-height: 100px;
}

* {box-sizing: border-box;}

.route404 {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 75px;
    text-align: center;
}
